import React from 'react';
import ReactDOM from 'react-dom';
import { motion } from 'framer-motion';

function App() {
    const handleClick = () => {
        const videoContainer = document.getElementById('video-container');

        // Show the video container
        videoContainer.style.display = 'block';

        // Remove any existing iframe
        const existingFrame = videoContainer.querySelector('iframe');
        if (existingFrame) {
            videoContainer.removeChild(existingFrame);
        }

        // Create and append the new iframe
        const frame = document.createElement('iframe');
        frame.src = '/video_feed';  // Flask route for the video feed
        frame.style.width = '100%';
        frame.style.height = '100%';  // Make sure it takes up the full height of the container
        frame.style.border = 'none';
        frame.classList.add('video-frame');
        videoContainer.appendChild(frame);
    };

    return (
        <div className="app-container">
            <h1>Eye Spy Object Detection</h1>
            <p>Struggling to track objects in real-time? Say hello to our top-notch object detector.</p>
            <motion.button
                style={buttonStyle}
                onClick={handleClick}
            >
                Start Object Detection
            </motion.button>
        </div>
    );
}

const buttonStyle = {
    backgroundColor: '#007bff',
    color: '#fff',
    padding: '15px 30px',
    fontSize: '18px',
    borderRadius: '8px',
    border: 'none',
    cursor: 'pointer',
};

ReactDOM.render(<App />, document.getElementById('root'));
